@font-face {
    font-family: "icons-turn-arrow";
    src: url('fonts/icons/turn-arrow.eot') format('embedded-opentype'), url('fonts/icons/turn-arrow.woff') format('woff'), url('fonts/icons/turn-arrow.ttf') format('truetype'), url('fonts/icons/turn-arrow.svg') format('svg');
}

html,
body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    background-color: #222;
    width: 100%;
    height: 100%;
}

.content {
    width: 100%;
    height: 100%;
}


/*stage start*/

.stage {
    position: relative;
    width: 100%;
    height: 680px;
}


/*stage end*/


/*image start*/

.img-sec {
    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden;
    background-color: #ddd;
    perspective: 1800px;
    -webkit-perspective: 1800px;
}


/*image end*/


/*controller start*/
.controller-nav {
    position: absolute;
    left: 0;
    bottom: 30px;
    z-index: 101;
    width: 100%;
    text-align: center;
}

.controller-unit {
    display: inline-block;
    width: 30px;
    height: 30px;
    margin: 0 5px;
    text-align: center;
    cursor: pointer;
    background-color: #aaa;
    border-radius: 50%;
    -webkit-transform: scale(.5);
        -ms-transform: scale(.5);
            transform: scale(.5);
    vertical-align: middle;
    -webkit-transition:background-color .3s,-webkit-transform .6s ease-in-out;
    transition:background-color .3s,-webkit-transform .6s ease-in-out;
    -o-transition:transform .6s ease-in-out,background-color .3s;
    transition:transform .6s ease-in-out,background-color .3s;
    transition:transform .6s ease-in-out,background-color .3s,-webkit-transform .6s ease-in-out; 
}

.is-center {
    -webkit-transform: scale(1);
        -ms-transform: scale(1);
            transform: scale(1);
    background-color: #888;
}
/*通过字体方式添加箭头，设置伪元素*/
.is-center::after {
    color: #fff;
    font-family: 'icons-turn-arrow';
    content: '\e600';
    line-height: 30px;
    font-size: 85%;
}
.c-is-inverse{
    background-color: #555;
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}


/*controller end*/

.img-figure {
    position: absolute;
    width: 260px;
    height: 360px;
    margin: 0;
    padding: 40px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    background-color: #fff;
    border: thin solid #0000FF;
    cursor: pointer;
    -webkit-transform-origin: 0 50% 0;
        -ms-transform-origin: 0 50% 0;
            transform-origin: 0 50% 0;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-transition: left .6s ease-in-out, top .6s ease-in-out, -webkit-transform .6s ease-in-out;
    transition: left .6s ease-in-out, top .6s ease-in-out, -webkit-transform .6s ease-in-out;
    -o-transition: transform .6s ease-in-out, left .6s ease-in-out, top .6s ease-in-out;
    transition: transform .6s ease-in-out, left .6s ease-in-out, top .6s ease-in-out;
    transition: transform .6s ease-in-out, left .6s ease-in-out, top .6s ease-in-out, -webkit-transform .6s ease-in-out;
}

.is-inverse {
    -webkit-transform: translate(320px) rotateY(180deg);
            transform: translate(320px) rotateY(180deg);
}

figcaption {
    text-align: center;
}

.img-title {
    margin: 20px 0 0 0;
    color: #a7a0a2;
    font-size: 16px;
}

.img-back {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding: 50px 40px;
    overflow: auto;
    color: #a7a0a2;
    font-size: 22px;
    line-height: 1.25;
    text-align: left;
    background-color: #fff;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    -webkit-transform: rotateY(180deg) translateZ(1px);
            transform: rotateY(180deg) translateZ(1px);
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
}

.img-back p {
    margin: 0;
}


